.main {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    width: 95%;
    margin-left: 2.5%;
}
.experienceContainer {
    padding: 20px;

}
.experienceContainer img {
    width: 80px;
    height: 80px;
    border-radius: 50%;
    position: absolute;
    margin-left: 20px;
    border: 3px solid #f44336;
    margin-top: 10px;
}
.experienceContainer h1 {
    margin-left: 120px;
    font-family: 'arial';
    color: #222;
    font-size: 22px;
}
.experienceContainer h3 {
    margin-left: 120px;
    font-family: 'arial';
    font-size: 18px;
    color: #666;
}
.experienceContainer ul {
    margin-left: 120px;
    font-family: 'arial';
    font-size: 16px;
    color: #222;
    padding: 2px;
}
#headerTitle {
    margin-top: 50px;
    margin-left: 20px;
    font-family: 'arial';
    color: #222;
    text-align: center;
    padding-top: 50px;
}
hr {
    width: 70%;
    margin-left: 100px;
    border-top: 1px solid lightgray;
}

.experienceContainer ul li{
    padding: 3px;
}
